import React, { useState, useEffect } from 'react';
import { Layout, Modal, Button, Form, Input, message, List, Card } from 'antd';
import { auth } from '../../config';
import { updateEmail, updatePassword, sendPasswordResetEmail, onAuthStateChanged, reauthenticateWithCredential, EmailAuthProvider } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

const { Content } = Layout;

const Themtaikhoan = () => {
    const [users, setUsers] = useState([]);
    const [isResetModalVisible, setIsResetModalVisible] = useState(false);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [form] = Form.useForm();
    const [selectedUser, setSelectedUser] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, (user) => {
            if (user) {
                setUsers([{ uid: user.uid, email: user.email }]);
            } else {
                navigate('/login');
            }
        });

        return () => unsubscribe();
    }, [navigate]);

    const handleResetModalCancel = () => {
        setIsResetModalVisible(false);
        setSelectedUser(null);
    };

    const handlePasswordReset = (user) => {
        setSelectedUser(user);
        setIsResetModalVisible(true);
    };

    const sendPasswordReset = async () => {
        try {
            await sendPasswordResetEmail(auth, selectedUser.email);
            toast.success('Đã gửi email đặt lại mật khẩu.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            setIsResetModalVisible(false);
        } catch (error) {
            console.error("Lỗi khi gửi email đặt lại mật khẩu:", error);
            message.error('Lỗi khi gửi email');
        }
    };

    const showModal = (user = null) => {
        setSelectedUser(user);
        if (user) {
            form.setFieldsValue({ email: user.email, password: '', currentPassword: '' });
        }
        setIsModalVisible(true);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
        setSelectedUser(null);
        form.resetFields();
    };

    const onFinish = async (values) => {
        const { email, password, currentPassword } = values;
        try {
            const user = auth.currentUser;
            if (user) {
                const credential = EmailAuthProvider.credential(user.email, currentPassword);
                await reauthenticateWithCredential(user, credential);
                
                if (email !== user.email) {
                    await updateEmail(user, email);
                }
                if (password) {
                    await updatePassword(user, password);
                }
                message.success('Cập nhật người dùng thành công');
                setUsers(prevUsers => prevUsers.map(u => u.uid === user.uid ? { ...u, email } : u));
                setIsModalVisible(false);
                form.resetFields();
            }
        } catch (error) {
            console.error("Error updating user:", error);
            if (error.code === 'auth/wrong-password') {
                message.error('Mật khẩu hiện tại không đúng');
            } else {
                message.error('Mật khẩu hiện tại không đúng');
            }
        }
    };

    return (
        <Layout className="site-layout">
            <title>Thông tin tài khoản NISO</title>
            <Content>
                <div className="site-layout-background">
                    <List
                        grid={{
                            gutter: 16,
                            column: 1
                        }}
                        dataSource={users}
                        renderItem={user => (
                            <List.Item>
                                <Card title={user.email} style={{ width: '100%' }}>
                                    <Button
                                        style={{ marginBottom: '10px', width: '100%' }}
                                        onClick={() => showModal(user)}
                                    >
                                        Đổi mật khẩu
                                    </Button>
                                    <Button
                                        style={{ width: '100%' }}
                                        onClick={() => handlePasswordReset(user)}
                                    >
                                        Gửi Email Đặt Lại Mật Khẩu
                                    </Button>
                                </Card>
                            </List.Item>
                        )}
                    />
                </div>
            </Content>
            <Modal
                title="Đổi mật khẩu"
                visible={isModalVisible}
                onCancel={handleCancel}
                footer={null}
            >
                <Form form={form} onFinish={onFinish} layout="vertical">
                    <Form.Item name="email" label="Email" rules={[{ required: true, type: 'email', message: 'Vui lòng nhập email hợp lệ!' }]}  >
                        <Input disabled/>
                    </Form.Item>
                    <Form.Item name="currentPassword" label="Mật khẩu hiện tại" rules={[{ required: true, message: 'Vui lòng nhập mật khẩu hiện tại!' }]}>
                        <Input.Password />
                    </Form.Item>
                    <Form.Item name="password" label="Mật khẩu mới" rules={[{ required: true, message: 'Vui lòng nhập mật khẩu mới!' }]}>
                        <Input.Password />
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            Cập nhật
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
            <Modal
                title="Đặt lại mật khẩu"
                visible={isResetModalVisible}
                onCancel={handleResetModalCancel}
                onOk={sendPasswordReset}
                okText="Gửi Email"
                cancelText="Hủy"
            >
                <p>Chúng tôi sẽ gửi cho bạn một email vào {selectedUser?.email} để đặt lại mật khẩu.</p>
            </Modal>
        </Layout>
    );
};

export default Themtaikhoan;